import React,{useState} from 'react';
import "./App.css"
// import AllDocViewer from './component/all-doc-viewer'
import AllDocViewer from 'all-doc-viewer-fixed';





export default function App() {

        const [url,setUrl] = useState("http://www.africau.edu/images/default/sample.pdf");
        const [dheight,setHeight] = useState('600');
        const [dwidth,setWidth] = useState('600');
        
        const [rurl,setRUrl] = useState(url);
        const [rheight,setRHeight] = useState(dwidth);
        const [rwidth,setRWidth] = useState(dheight);
        

        const update = ()=>{
            setUrl(rurl);
            setHeight(rheight);
            setWidth(rwidth);
            console.log(rheight)
        }

 
        return (
            <React.Fragment>
                <div className="block-left">
                    <div class="full-screen-container">
                        <div class="login-container">
                            <h3 class="login-title">Welcome</h3>
                            <form>
                                <div class="input-group">
                                    <label>URL</label>
                                    <input type="text" defaultValue={url} onChange={(e) => setRUrl(e.target.value)} />
                                </div>
                                <div class="input-group">
                                    <label>Height (in px)</label>
                                    <input type="text" defaultValue={dheight} onChange={(e) => setRHeight(e.target.value)} />
                                </div>
                                <div class="input-group">
                                    <label>Width (in px)</label>
                                    <input type="text" defaultValue={dwidth} onChange={(e) => setRWidth(e.target.value)} />
                                </div>
                                <button type="button" onClick={()=>update()}  class="login-button">Load</button>
                            </form>
                            <div className="footer-badge">
                                <a href="https://www.npmjs.com/package/all-doc-viewer-fixed" target="_blank">&#10939; GO TO NPM</a>
                            </div>
                        </div>
                    </div>

                </div>
                <div className="block-right">
                    <AllDocViewer url={url} height={dheight} width={dwidth} />
                </div>


            </React.Fragment>
        );
}